/**
 * 
 */
export default abstract class SessionCacheBase {
  constructor(protected readonly appName: string) {
    this.buildKey = this.buildKey.bind(this);
  }

  /**
   * 
   * @param key key
   */
  protected buildKey(key: string) {
    return `${this.appName}_SessionCache_${key}`;
  }

  /**
   * 
   * @param key key
   */
  abstract get<T = string>(key: string): T | undefined;
  /**
   * 
   * @param key key
   * @param value 
   */
  abstract set<T = string>(key: string, value: T): void;
  /**
   * 
   * @param key key
   */
  abstract remove(key: string): void;
  /**
   * 
   * 1. web
   */
  abstract clear(): void;
}
